.whole_page{
    height: 100vh;
    background-color: azure;
}


.section_body{
    display: flex;
    justify-content: space-between;
}

.left_part{
    width: 70%;
    padding: 25px;
    height: 85vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.right_part{
    width: 50%;
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
p{
    margin-top:15px;
}
.btn_group{
    margin-top: 30px;
}
.btn_group button{
    padding: 12px;
    margin-right: 20px;
    text-transform: uppercase;
    font-weight: 700;
}
.btn_group :nth-child(1){
    background-color: white;
}
.btn_group :nth-child(2){
    background-color: black;
    color:white;
}

.right_part{
    display: flex;
    justify-content: center;
}

.right_part img{
    width: 400px;
    height: 400px;
}